.Navigation ul{
    list-style: none;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding-left: 0;
    padding-top: 13px;
}
.Navigation li{
    text-transform: uppercase;
    font-size: 14px;
    color: #42495b;
    -webkit-transition: all 0.3s;
    -o-transition: all 0.3s;
    transition: all 0.3s;
    cursor: pointer;
    margin-right: 58px;
    margin-bottom: 20px;
}
.Navigation li:hover{
    color: #07d79b;
}
.Navigation .Navigation--active{
    color: #07d79b;
}


